import React, {FC} from 'react';
import {useAppSelector} from "../../hooks/redux";
import ReactDOM from "react-dom";
import {Modal} from "react-bootstrap";
import {Heartbreak} from "react-bootstrap-icons";

interface ICardModal{
    show: boolean,
    closeHandler: () => void,
}
const CardModal:FC<ICardModal> = ({show, closeHandler}) => {
    const {isAuthorized} = useAppSelector(state => state.authorizationReducer)
    if (!show) return null
    return ReactDOM.createPortal(
        <Modal show={show} onHide={closeHandler}>
            <Modal.Header closeButton>
                <Modal.Title>Cart</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                {!isAuthorized ? <h3 className={'text-center my-4'}>Ви не авторизовані <Heartbreak className={'mb-1'}/></h3> : <></>}
            </Modal.Body>
        </Modal>,
        document.getElementById('root') as HTMLElement
    );
};

export default CardModal;