import React from 'react';
import {Google, Mailbox, Person, PersonBadgeFill} from "react-bootstrap-icons";
import {useAppDispatch, useAppSelector} from "../../hooks/redux";
import {sign_out} from "../../redux/action_creators/authorization_action_creator";

const Cabinet = () => {
    const user = useAppSelector(state => state.authorizationReducer.user)
    const dispatch = useAppDispatch()

    const signOutHandler = () => {
        dispatch(sign_out())
    }

    return (
        <>
            <div className={"container"}>
                <h2 className={"text-center display-6 mt-5"}>Personal Account</h2>
                <p onClick={signOutHandler} style={{cursor: 'pointer'}} className={"text-decoration-underline text-center"}>Sign Out</p>
            </div>
            <div className={"container"}>
                <div className={"w-50 mx-auto mt-5"}>
                    <div className="input-group mb-3">
                        <span className="input-group-text" id="basic-addon1"><PersonBadgeFill height={20} width={20}/></span>
                        <input disabled value={user?.username} type="text" className="form-control" placeholder="Username"/>
                    </div>
                    {/*<div className="input-group mb-3">*/}
                    {/*    <span className="input-group-text" id="basic-addon1"><Bricks height={20} width={20}/></span>*/}
                    {/*    <input disabled value={} type="text" className="form-control" placeholder="Password"/>*/}
                    {/*</div>*/}
                    <div className="input-group mb-3">
                        <span className="input-group-text" id="basic-addon1"><Person height={20} width={20}/>1</span>
                        <input disabled value={user?.name} type="text" className="form-control" placeholder="First Name"/>
                    </div>
                    <div className="input-group mb-3">
                        <span className="input-group-text" id="basic-addon1"><Person height={20} width={20}/>2</span>
                        <input disabled value={user?.surname} type="text" className="form-control" placeholder="Last Name"/>
                    </div>
                    <div className="input-group mb-3">
                        <span className="input-group-text" id="basic-addon1"><Google height={20} width={20}/></span>
                        <input disabled value={user?.email} type="text" className="form-control" placeholder="Email"/>
                    </div>
                    <div className="input-group mb-3">
                        <span className="input-group-text" id="basic-addon1"><Mailbox height={20} width={20}/></span>
                        <input disabled value={user?.adress ?? ""} type="text" className="form-control" placeholder="Adress"/>
                    </div>
                    <p className={"text-center"}><button className={"btn btn-primary w-100"}>Update</button></p>
                </div>
            </div>
        </>
    );
};

export default Cabinet;